*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: DM sans, san serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.list, .footer__list-items, .list--butoons {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  list-style: none;
  display: flex;
}

.container {
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding: 0 16px;
  }

  .list, .footer__list-items, .list--butoons {
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.menu {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}

.mobile-menu__link {
  color: #fff;
  border-bottom: 32px;
  font-size: 14px;
  line-height: 1.12em;
  text-decoration: none;
  display: block;
}

.mobile-menu__link:hover, .mobile-menu__link:active {
  color: getColor(selected);
}

.menu-toggle {
  width: 30px;
  height: 30px;
  fill: getColor(accent);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  transition-property: fill;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 15px;
  right: 16px;
}

.menu-toggle:hover, .menu-toggle:focus {
  fill: getColor(selected);
}

@media (min-width: 768px) {
  .menu-toggle {
    display: none;
    position: fixed;
  }
}

.menu-container {
  width: 250px;
  height: 100vh;
  z-index: 999;
  background-color: #e17992;
  flex-direction: column;
  align-items: flex-start;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .menu-toggle {
  position: absolute;
  top: 16px;
  right: 16px;
}

.mobile-menu {
  margin: 0;
  padding: 0;
  list-style: none;
}

.mobile-menu .link {
  color: #fff;
  border: 1px solid #e18298;
  padding: 10px;
  text-decoration: none;
}

.mobile-menu_tel {
  letter-spacing: 2%;
  text-align: left;
  font-size: 40px;
  font-weight: 500;
}

.Mobile-menu_contacts {
  justify-content: flex-start;
  align-items: flex-start;
  padding-left: 10px;
}

.mobile-menu_mail {
  letter-spacing: 2%;
  text-align: left;
  font-size: 28px;
  font-weight: 500;
}

.mobile-menu_socials {
  flex-direction: row;
  justify-content: flex-start;
  margin-top: 32px;
}

.mobile-menu__footer {
  color: getColor(selected);
  font-size: 18px;
  text-decoration: none;
}

.mobilie-menu__box {
  border-right: 1px solid getColor(icons);
  padding-left: 10px;
  padding-right: 10px;
}

.mobilie-menu__box:last-child {
  border-right: 0;
}

.button--buy {
  width: 209px;
  color: #d41443;
  border: 0;
  border-radius: 32px;
  margin-top: 60px;
  margin-bottom: 20px;
  margin-left: 20px;
  padding: 15px;
  font-size: 16px;
  font-weight: 700;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

.mobile-menu__item {
  width: 250px;
  border-top: 1px solid #e18298;
  align-items: start;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;
  display: flex;
}

.mobile-menu__item:last-child {
  border-bottom: 1px solid #e18298;
}

.mobile-menu__icon, .icon--location, .fotter__social-icon {
  fill: rgba(0, 0, 0, 0);
  stroke: #d41443;
  margin-left: 15px;
}

